@charset "UTF-8";
/* Roboto Regular */
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Regular.eot");
  src: local("Roboto"), local("Roboto Regular"), local("Roboto-Regular"), url("../fonts/Roboto/Roboto-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* Roboto Medium */
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Medium.eot");
  src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/Roboto/Roboto-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto/Roboto-Medium.woff") format("woff"), url("../fonts/Roboto/Roboto-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

/* Roboto Bold */
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Bold.eot");
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/Roboto/Roboto-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff"), url("../fonts/Roboto/Roboto-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

html, body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  text-size-adjust: none;
  background-color: #fff;
  color: #000;
  position: relative;
}

* {
  box-sizing: border-box;
}

img {
  border: none;
  max-width: 100%;
}

button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button:focus, button:active {
  outline: 0px solid transparent;
}

h2, h3 {
  margin: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.container {
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  max-width: 1040px;
  padding: 0 20px;
}

.container-small {
  max-width: 776px;
  width: 100%;
  margin: 0 auto;
}

.wrapper {
  min-height: 100vh;
  min-height: calc(var(--vh) * 100);
  display: flex;
  flex-direction: column;
}

.content {
  flex-grow: 1;
}

.header {
  background-image: url("../images/background2.jpg");
}

.header__logo {
  display: block;
  margin: 72px 0 40px auto;
}

.header-inner {
  background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
  width: 100%;
  padding: 24px 0;
}

.header-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  color: #fff;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}

.header-content__text {
  flex-shrink: 0;
}

.header-content__text_right {
  text-align: right;
  flex-shrink: 0;
}

.header-content__span {
  display: block;
  font-size: 32px;
  line-height: 40px;
  font-weight: bold;
  margin-top: 4px;
}

.header-content__img {
  position: relative;
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
}

.header-content__img img {
  position: absolute;
}

.header-content__gorkiy {
  position: relative;
  top: -90px;
}

.main {
  background-image: url("../images/background.jpg");
  background-position: top center;
  padding: 40px 0 120px 0;
}

.main__link {
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  text-decoration-line: underline;
  color: #B51F24;
}

.main__title {
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
  text-transform: uppercase;
  color: #B51F24;
  margin: 64px 0 0 0;
}

.main__list {
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: #1F1F1F;
  margin: 24px 0 0 0;
}

.main-text {
  margin-top: 32px;
}

.main-text__paragraph {
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: #1F1F1F;
  margin: 24px 0 0 0;
}

.social {
  display: flex;
  margin-bottom: 65px;
}

.social__icon {
  width: 48px;
  height: 48px;
  display: block;
  margin-right: 16px;
}

.main-photo {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
}

.main-photo__img {
  display: block;
}

.main-inner {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 40px;
  text-align: center;
}

.main-inner__title {
  width: 100%;
  background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
  text-align: center;
  padding: 16px 112px;
}

.main-inner__h1 {
  color: #fff;
  font-size: 32px;
  line-height: 40px;
  text-transform: uppercase;
  margin: 0;
}

.main-inner__btn {
  display: inline-block;
  width: 206px;
  height: 42px;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  font-weight: normal;
  background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
  text-decoration: none;
  cursor: pointer;
  padding: 8px 0;
  margin: 24px;
}

.politika-kd {
  padding-top: 63px;
}

.politika-kd__paragraph {
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: #1F1F1F;
  margin: 32px 0 0 0;
}

.politika-kd__btn {
  text-align: center;
}

.politika-kd__btn-link {
  width: 220px;
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: #FFFFFF;
  margin: 40px 0 0 0;
}

.main-spruces_left {
  position: absolute;
  bottom: 0;
  pointer-events: none;
  left: -15px;
  top: 192px;
}

.main-spruces_right {
  position: absolute;
  bottom: 0;
  pointer-events: none;
  right: -15px;
  top: 192px;
}

.main-section {
  margin-top: 120px;
}

.main-section__gallery {
  margin-top: 56px;
}

.subtitle {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  color: #B51F24;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
}

.subtitle::before {
  flex-shrink: 0;
  content: '';
  display: block;
  width: 88px;
  height: 4px;
  background: linear-gradient(270deg, #B51F24 0%, rgba(181, 31, 36, 0) 100%);
  margin: 13px 24px 0 0;
}

.main-text {
  margin: 32px 0;
}

.main-text__paragraph {
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  margin-top: 24px;
}

.accent {
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  color: #B51F24;
  text-transform: uppercase;
  text-align: center;
}

.accent::before {
  content: '';
  display: block;
  width: 100%;
  height: 4px;
  background: linear-gradient(270deg, rgba(181, 31, 36, 0) 0.52%, #B51F24 50%, rgba(181, 31, 36, 0) 100%);
  margin: 0 auto 24px auto;
}

.accent::after {
  content: '';
  display: block;
  width: 100%;
  height: 4px;
  background: linear-gradient(270deg, rgba(181, 31, 36, 0) 0.52%, #B51F24 50%, rgba(181, 31, 36, 0) 100%);
  margin: 24px auto 0 auto;
}

.host-events {
  display: flex;
  justify-content: space-between;
  margin-top: 56px;
}

.host-events__wrapper {
  display: flex;
  flex-direction: column;
}

.host-events__name {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  text-align: center;
  text-transform: uppercase;
  margin-top: 24px;
}

.video {
  filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
  margin-top: 56px;
  border-radius: 8px;
}

.video__link {
  position: relative;
  text-align: center;
  display: block;
}

.video__link::before {
  display: inline-block;
  width: 80px;
  height: 80px;
  content: '';
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
  background: url("../images/play-btn.svg") center no-repeat;
  z-index: 5;
  background-size: contain;
}

.video__link::after {
  content: '';
  position: absolute;
  width: 120px;
  height: 120px;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
  background: linear-gradient(88.99deg, #8E181B 23.54%, #C92F3E 50.28%, #B51F24 76.46%);
  opacity: 0.5;
  border-radius: 50%;
  z-index: 1;
}

.video__img {
  display: block;
  border-radius: 8px;
  text-align: center;
}

.gallery {
  display: flex;
  justify-content: space-between;
}

.fancybox__item {
  filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
  border-radius: 8px;
}

.video-gallery {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 56px;
}

.video-gallery__link {
  display: block;
  filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
  border-radius: 8px;
  position: relative;
  text-align: center;
}

.video-gallery__link::before {
  display: inline-block;
  width: 48px;
  height: 48px;
  content: '';
  position: absolute;
  background: url("../images/play-btn.svg") center no-repeat;
  background-size: contain;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
  z-index: 10;
}

.video-gallery__link::after {
  content: '';
  position: absolute;
  width: 72px;
  height: 72px;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
  background: linear-gradient(88.99deg, #8E181B 23.54%, #C92F3E 50.28%, #B51F24 76.46%);
  opacity: 0.5;
  border-radius: 50%;
  z-index: 1;
}

.form {
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(68, 0, 0, 0.15);
  background: url("../images/logo-gorkiy-live-grey.svg") center no-repeat;
  border-radius: 8px;
  padding: 48px 108px;
  margin: 56px auto 0 auto;
}

.form__group {
  display: flex;
  flex-direction: column;
  margin-top: 32px;
}

.form__group:first-child {
  margin-top: 0;
}

.form__group-politica-kd {
  margin-top: 24px;
}

.form__group-submit {
  text-align: center;
}

.form__label {
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: #000;
}

.form__input {
  border: none;
  background-color: transparent;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: inherit;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  border-radius: 0;
  appearance: none;
  width: 100%;
  height: 42px;
  background: #FFFFFF;
  border: 1px solid #B51F24;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 400;
  padding: 8px 16px;
  margin-top: 8px;
  line-height: 26px;
}

.form__input::placeholder {
  opacity: 1;
  color: inherit;
}

.form__input:focus::placeholder {
  opacity: 0;
}

.form__input::placeholder {
  color: #DFDFDF;
}

.form__textarea {
  border: none;
  background-color: transparent;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: inherit;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  border-radius: 0;
  appearance: none;
  resize: none;
  width: 100%;
  height: 84px;
  background: #FFFFFF;
  border: 1px solid #B51F24;
  border-radius: 8px;
  padding: 8px 16px;
  margin-top: 8px;
  font-size: 18px;
  min-height: 42px;
  max-height: 180px;
  resize: vertical;
}

.form__textarea::placeholder {
  opacity: 1;
  color: inherit;
}

.form__textarea:focus::placeholder {
  opacity: 0;
}

.form__textarea::placeholder {
  color: #DFDFDF;
}

.form__paragraph {
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  margin: 0;
}

.form__link {
  color: #B51F24;
}

.form__submit {
  border: none;
  background-color: transparent;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: inherit;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  border-radius: 0;
  appearance: none;
  width: 185px;
  height: 42px;
  border-radius: 8px;
  border: none;
  font-size: 18px;
  color: #FFFFFF;
  background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
  margin-top: 24px;
  cursor: pointer;
}

.form__submit::placeholder {
  opacity: 1;
  color: inherit;
}

.form__submit:focus::placeholder {
  opacity: 0;
}

.contacts {
  width: 100%;
  background: linear-gradient(88.99deg, #8E181B 23.54%, #C92F3E 50.28%, #B51F24 76.46%);
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  padding: 24px 0;
}

.contacts__title {
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
}

.contacts__list {
  display: flex;
  justify-content: center;
  list-style-type: none;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  padding-right: 32px;
  margin: 4px 0 0 0;
}

.contacts__item {
  flex-shrink: 0;
}

.contacts__item:not(:last-child) {
  padding-right: 32px;
}

.contacts__line {
  content: "";
  width: 2px;
  height: 26px;
  background: #FFFFFF;
  border-radius: 2px;
  padding-right: 0;
  margin-right: 32px;
}

.contacts__phone {
  background: url("../images/icon-phone.svg") left no-repeat;
  padding-left: 40px;
}

.contacts__email {
  background: url("../images/icon-email.svg") left center no-repeat;
  padding-left: 40px;
}

.contacts__link {
  text-decoration: none;
  color: #fff;
  display: block;
}

.footer {
  background-image: url("../images/background2.jpg");
  padding: 40px 0;
}

.footer-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer a:not(:last-child) {
  margin-right: 16px;
}

.footer .social__social-icon {
  margin: 0;
}

/* ПОДКЛЮЧЕНИЕ СТИЛЕЙ АДАПТИВА */
@media screen and (max-width: 1023px) {
  .container {
    max-width: 960px;
  }
  .container-small {
    max-width: 696px;
  }
  .host-events {
    justify-content: space-around;
  }
  .host-events__img {
    width: 200px;
    height: 200px;
  }
  .host-events__name {
    font-size: 20px;
    line-height: 28px;
  }
  .fancybox__img {
    width: 90%;
  }
  .video-gallery__img {
    width: 444px;
  }
}

@media screen and (max-width: 959px) {
  .container {
    max-width: 768px;
  }
  .container-small {
    max-width: 520px;
  }
  .header-content {
    font-size: 16px;
    line-height: 24px;
  }
  .header-content__span {
    font-size: 28px;
    line-height: 36px;
  }
  .main-inner {
    bottom: 0;
  }
  .main-inner__h1 {
    font-size: 23px;
    line-height: 31px;
  }
  .main-spruces_left, .main-spruces_right {
    top: 110px;
  }
  .subtitle {
    font-size: 22px;
    line-height: 30px;
  }
  .subtitle::before {
    width: 80px;
    height: 3px;
  }
  .main-text__paragraph {
    font-size: 16px;
    line-height: 24px;
  }
  .accent {
    font-size: 16px;
    line-height: 24px;
  }
  .video__link::before {
    width: 60px;
    height: 60px;
  }
  .video__link::after {
    width: 100px;
    height: 100px;
  }
  .video-gallery__img {
    width: 354px;
  }
  .contacts__list {
    padding-right: 20px;
  }
  .form {
    padding: 30px 40px;
  }
  .form__paragraph {
    font-size: 16px;
    line-height: 26px;
  }
  .contacts__title {
    font-size: 28px;
    line-height: 36px;
  }
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 600px;
  }
  .header-content__img {
    display: none;
  }
  .header-content__text_right {
    text-align: left;
  }
  .main {
    padding-bottom: 80px;
  }
  .main-section {
    margin-top: 100px;
  }
  .social {
    margin-bottom: 43px;
  }
  .social__icon {
    width: 38px;
    height: 38px;
  }
  .main-inner__title {
    padding: 16px 65px;
  }
  .main-inner__h1 {
    font-size: 18px;
    line-height: 26px;
  }
  .main-inner__btn {
    width: 196px;
    height: 38px;
    font-size: 16px;
  }
  .main-spruces_left, .main-spruces_right {
    width: 200px;
  }
  .host-events__img {
    width: 170px;
    height: 170px;
  }
  .host-events__name {
    font-size: 16px;
  }
  .video-gallery__link {
    display: block;
    margin: 0 auto 0 auto;
  }
  .video-gallery__link:last-child {
    margin-top: 20px;
  }
  .contacts__title {
    font-size: 20px;
    line-height: 28px;
  }
  .contacts__list {
    flex-direction: column;
    align-items: center;
  }
  .contacts__link {
    margin-left: -50px;
  }
  .contacts__line {
    display: none;
  }
}

.main__title {
  font-size: 26px;
  line-height: 34px;
  margin: 55px 0 0;
}

.politika-kd__paragraph {
  font-size: 16px;
  line-height: 24px;
}

.main-text__paragraph, .main__list {
  font-size: 16px;
  line-height: 24px;
}

@media screen and (max-width: 599px) {
  .container {
    max-width: 480px;
  }
  .header__logo {
    margin: 72px auto 40px auto;
  }
  .header-content {
    flex-direction: column;
    justify-content: center;
  }
  .header-content__text {
    text-align: center;
  }
  .header-content__img {
    flex-grow: 0;
  }
  .header-content__text_right {
    margin-top: 24px;
    text-align: center;
  }
  .main-inner {
    position: static;
  }
  .main-inner__title {
    padding: 16px 20px;
  }
  .main-spruces_left, .main-spruces_right {
    top: 215px;
  }
  .subtitle {
    display: block;
    font-size: 20px;
    line-height: 26px;
    text-align: center;
  }
  .subtitle::before {
    width: 0;
    height: 0;
    margin: 0;
  }
  .host-events {
    flex-direction: column;
  }
  .host-events__wrapper:not(:first-child) {
    margin-top: 32px;
  }
  .host-events__img {
    width: 140px;
    height: 140px;
    margin: 0 auto;
  }
  .video__link::before {
    width: 40px;
    height: 40px;
  }
  .video__link::after {
    width: 70px;
    height: 70px;
  }
  .video-gallery__link::before {
    width: 35px;
    height: 35px;
  }
  .video-gallery__link::after {
    width: 60px;
    height: 60px;
  }
  .form {
    padding: 30px 20px;
  }
  .form__group {
    margin-top: 24px;
  }
  .footer-inner {
    flex-direction: column;
  }
  .footer-inner .social {
    margin: 20px 0;
  }
}

ol, ul {
  padding-left: 20px;
}

.main__link {
  font-size: 16px;
}

@media screen and (max-width: 479px) {
  .container {
    max-width: 100%;
  }
  .main-section {
    margin-top: 80px;
  }
  .header-content__span {
    font-size: 24px;
    line-height: 28px;
  }
  .host-events__name {
    margin-top: 15px;
  }
}

.main__title {
  font-size: 20px;
}

@media screen and (max-width: 379px) {
  .gallery {
    flex-direction: column;
  }
  .fancybox__img {
    width: 60%;
  }
  .form__label {
    font-size: 16px;
  }
  .form__input {
    height: 35px;
  }
  __paragraph {
    font-size: 14px;
    line-height: 18px;
  }
  .contacts__list {
    font-size: 16px;
    padding-right: 0;
    margin-top: 10px;
  }
}

/* НИЖЕ СТЕЛЕЙ НЕ ДОЛЖНО БЫТЬ!!! */
