@import "_variables";
@import "_mixins";
// @import "_keyframes";
@import "_fonts";
// @import "../lib/libs/slick-carousel/slick/slick.scss";

html, body {
	font-family: 'Roboto', sans-serif;
	margin: 0;
	padding: 0;
	text-size-adjust:none;
	background-color: $color-white;
	color: $color-black;
	position: relative;
}
* {
	box-sizing: border-box;
}
img {
	border: none;
	max-width: 100%;
}
button {
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	&:focus,
	&:active{
		outline: 0px solid transparent;
	}
}

h2, h3 {
	margin: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.container {
	margin: 0 auto;
	width: 100%;
	min-width: 320px;
	max-width: 1040px;
	padding: 0 20px;	
}

.container-small {
	max-width: 776px;
	width: 100%;
	margin: 0 auto;
}

.wrapper {
	min-height: 100vh;
	min-height: calc(var(--vh) * 100);
	display: flex;
	flex-direction: column;
}
.content{
	flex-grow: 1;
}

.header {
	background-image: url('../images/background2.jpg');
	&__logo {
		display: block;
		margin: 72px 0 40px auto;
	}
	&-inner {
		background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
		width: 100%;
		padding: 24px 0;
	}
	&-content {
		position: relative;
		display: flex;
		justify-content: space-between;
		color: #fff;
		font-size: 18px;
		line-height: 26px;
		text-transform: uppercase;
		&__text {
			flex-shrink: 0;
			&_right {
				text-align: right;
				flex-shrink: 0;
			}
		}
		&__span {
			display: block;
			font-size: 32px;
			line-height: 40px;
			font-weight: bold;
			margin-top: 4px;
		}
		&__img {
			position: relative;
			display: flex;
			flex-grow: 1;
			justify-content: center;
			align-items: center;
			img {
				position: absolute;
			}
		}
		&__gorkiy {
			position: relative;
			top: -90px;
		}
	}	
}

.main {
	background-image: url('../images/background.jpg');
	background-position: top center;
	padding: 40px 0 120px 0;
	&__link {
		font-weight: normal;
		font-size: 18px;
		line-height: 26px;
		text-decoration-line: underline;
		color: #B51F24;
	}
	&__title {
		font-weight: bold;
		font-size: 32px;
		line-height: 40px;
		text-transform: uppercase;
		color: #B51F24;
		margin: 64px 0 0 0;
	}
	&__list {
		font-weight: normal;
		font-size: 18px;
		line-height: 26px;
		color: #1F1F1F;
		margin: 24px 0 0 0;
	}
}

.main-text {
	margin-top: 32px;
	&__paragraph {
		font-weight: normal;
		font-size: 18px;
		line-height: 26px;
		color: #1F1F1F;
		margin: 24px 0 0 0;
	}
}

.social {
	display: flex;
	margin-bottom: 65px;
	&__icon {
		width: 48px;
		height: 48px;
		display: block;
		margin-right: 16px;
	}
}


.main-photo {
	position: relative;
	overflow: hidden;
	border-radius: 8px;
	filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
	&__img {
		display: block;
	}
}


.main-inner {
	width: 100%;
	position: absolute;
	left: 0;
	bottom: 40px;
	text-align: center;
	&__title {
		width: 100%;
		background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
		text-align: center;
		padding: 16px 112px;
	}
	&__h1 {
		color: #fff;
		font-size: 32px;
		line-height: 40px;
		text-transform: uppercase;
		margin: 0;
	}
	&__btn {
		display: inline-block;
		width: 206px;
		height: 42px;
		color: #fff;
		border: none;
		border-radius: 8px;
		font-size: 18px;
		font-weight: normal;
		background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
		text-decoration: none;
		cursor: pointer;
		padding: 8px 0;
		margin: 24px;
	}
}

.politika-kd {
	padding-top: 63px;
	&__paragraph {
		font-weight: normal;
		font-size: 18px;
		line-height: 26px;
		color: #1F1F1F;
		margin: 32px 0 0 0;
	}
	&__btn {
		text-align: center;
		&-link {
			width: 220px;
			font-weight: normal;
			font-size: 18px;
			line-height: 26px;
			color: #FFFFFF;
			margin: 40px 0 0 0;
		}
	}
}


.main-spruces_left {
	position: absolute;
	bottom: 0;
	pointer-events: none;
	left: -15px;
    top: 192px;
}


.main-spruces_right {
	position: absolute;
	bottom: 0;
	pointer-events: none;
	right: -15px;
    top: 192px;
}

.main-section {
	margin-top: 120px;
	&__gallery {
		margin-top: 56px;
	}
}

.subtitle {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	color: #B51F24;
	font-weight: 500;
	font-size: 24px;
	line-height: 32px;
	&::before {
		flex-shrink: 0;
		content: '';
		display: block;
		width: 88px;
		height: 4px;
		background: linear-gradient(270deg, #B51F24 0%, rgba(181, 31, 36, 0) 100%);
		margin: 13px 24px 0 0;
	}
}


.main-text {
	margin: 32px 0;
	&__paragraph {
		font-size: 18px;
		line-height: 26px;
		color: #000000;
		margin-top: 24px;
	}
}

.accent {
	font-weight: 500;
	font-size: 18px;
	line-height: 26px;
	color: #B51F24;
	text-transform: uppercase;
	text-align: center;
	&::before {
		content: '';
		display: block;
		width: 100%;
		height: 4px;
		background: linear-gradient(270deg, rgba(181, 31, 36, 0) 0.52%, #B51F24 50%, rgba(181, 31, 36, 0) 100%);
		margin: 0 auto 24px auto;
	}
	&::after {
		content: '';
		display: block;
		width: 100%;
		height: 4px;
		background: linear-gradient(270deg, rgba(181, 31, 36, 0) 0.52%, #B51F24 50%, rgba(181, 31, 36, 0) 100%);
		margin: 24px auto 0 auto;
	}
}

.host-events {
	display: flex;
	justify-content: space-between;
	margin-top: 56px;
	&__wrapper {
		display: flex;
		flex-direction: column;
	}
	&__name {
		font-weight: 500;
		font-size: 24px;
		line-height: 32px;
		text-align: center;
		text-transform: uppercase;
		margin-top: 24px;
	}
}

.video {
	filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
	margin-top: 56px;
	border-radius: 8px;
	&__link {
		position: relative;
		text-align: center;
		display: block;
		&::before {
			display: inline-block;
			width: 80px;
			height: 80px;
			content: '';
			position: absolute;
			bottom: 50%;
			left: 50%;
			transform: translate(-50%, 50%);
			background: url('../images/play-btn.svg') center no-repeat;
			z-index: 5;
			background-size: contain;
		}
		&::after {
			content: '';
			position: absolute;
			width: 120px;
			height: 120px;
			bottom: 50%;
			left: 50%;
			transform: translate(-50%, 50%);
			background: linear-gradient(88.99deg, #8E181B 23.54%, #C92F3E 50.28%, #B51F24 76.46%);
			opacity: 0.5;
			border-radius: 50%;
			z-index: 1;
		}
	}
	&__img {
		display: block;
		border-radius: 8px;
		text-align: center;
	}
		

}

.gallery {
	display: flex;
	justify-content: space-between;
}

.fancybox__item {
	filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
	border-radius: 8px;
}

.video-gallery {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-top: 56px;
	&__link {
		display: block;
		filter: drop-shadow(0px 0px 25px rgba(68, 0, 0, 0.15));
		border-radius: 8px;
		position: relative;
		text-align: center;
		&::before {
			display: inline-block;
			width: 48px;
			height: 48px;
			content: '';
			position: absolute;
			background: url('../images/play-btn.svg') center no-repeat;
			background-size: contain;
			bottom: 50%;
			left: 50%;
			transform: translate(-50%, 50%);
			z-index: 10;
		}
		&::after {
			content: '';
			position: absolute;
			width: 72px;
			height: 72px;
			bottom: 50%;
			left: 50%;
			transform: translate(-50%, 50%);
			background: linear-gradient(88.99deg, #8E181B 23.54%, #C92F3E 50.28%, #B51F24 76.46%);
			opacity: 0.5;
			border-radius: 50%;
			z-index: 1;
		}
	}
}

.form {
	background: #FFFFFF;
	box-shadow: 0px 0px 25px rgba(68, 0, 0, 0.15);
	background: url('../images/logo-gorkiy-live-grey.svg') center no-repeat;
	border-radius: 8px;
	padding: 48px 108px;
	margin: 56px auto 0 auto;
	&__group {
		display: flex;
		flex-direction: column;
		margin-top: 32px;
		&:first-child {
			margin-top: 0;
		}
		&-politica-kd {
			margin-top: 24px;
		}
		&-submit {
			text-align: center;
		}
	}
	&__label {
		font-weight: normal;
		font-size: 18px;
		line-height: 26px;
		color:  #000;
	}
	&__input {
		@include reset-input;
		width: 100%;
		height: 42px;
		background: #FFFFFF;
		border: 1px solid #B51F24;
		border-radius: 8px;
		font-size: 18px;
		font-weight: 400;
		padding: 8px 16px;
		margin-top: 8px;
		line-height: 26px;
		&::placeholder{
			color: #DFDFDF;
		}

	}
	&__textarea {
		@include reset-textarea;
		width: 100%;
		height: 84px;
		background: #FFFFFF;
		border: 1px solid #B51F24;
		border-radius: 8px;
		padding: 8px 16px;
		margin-top: 8px;
		font-size: 18px;
		min-height: 42px;
		max-height: 180px;
		resize: vertical;
		&::placeholder{
			color: #DFDFDF;
		}
	}
	&__paragraph {
		font-size: 18px;
		line-height: 26px;
		color: #000000;
		margin: 0;
	}
	&__link {
		color: #B51F24;
	}
	&__submit {
		@include reset-input;
		width: 185px;
		height: 42px;
		border-radius: 8px;
		border: none;
		font-size: 18px;
		color: #FFFFFF;
		background: linear-gradient(90deg, #8E181B, #C92F3E, #B51F24);
		margin-top: 24px;
		cursor: pointer;
	}
}

.contacts {
	width: 100%;
	background: linear-gradient(88.99deg, #8E181B 23.54%, #C92F3E 50.28%, #B51F24 76.46%);
	color: #fff;
	text-transform: uppercase;
	text-align: center;
	padding: 24px 0;
	&__title {
		font-weight: bold;
		font-size: 32px;
		line-height: 40px;
	}
	&__list {
		display: flex;
		justify-content: center;
		list-style-type: none;
		font-size: 18px;
		font-weight: 500;
		line-height: 26px;
		padding-right: 32px;
		margin: 4px 0 0 0;
	}
	&__item{
		flex-shrink: 0;
	}
	&__item:not(:last-child) {
		padding-right: 32px;
	}
	&__line {
		content: "";
		width: 2px;
		height: 26px;
		background: #FFFFFF;
		border-radius: 2px;
		padding-right: 0;
		margin-right: 32px;
	}
	&__phone {
		background: url('../images/icon-phone.svg') left no-repeat;
		padding-left: 40px;
	}
	&__email {
		background: url('../images/icon-email.svg') left center no-repeat;
		padding-left: 40px;
	}
	&__link {
		text-decoration: none;
		color: #fff;
		display: block;
	}
}

.footer {
	background-image: url('../images/background2.jpg');
	padding: 40px 0;
	&-inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	a:not(:last-child) {
		margin-right: 16px;
	}
	.social {
		&__social-icon {
			margin: 0;
		}
	}
}



