@media screen and (max-width: 1179px){
	.container{
		// max-width: 1024px;
	}
}
@media screen and (max-width: 1023px){
	.container{
		max-width: 960px;
	}
	.container-small {
		max-width: 696px;
	}
	.host-events {
		justify-content: space-around;
		&__img {
			width: 200px;
			height: 200px;
		}
		&__name {
			font-size: 20px;
			line-height: 28px;
		}
	}
	.fancybox__img {
		width: 90%;
	}
	.video-gallery__img {
		width: 444px;
	}
}
@media screen and (max-width: 959px){
	.container{
		max-width: 768px;
	}
	.container-small {
		max-width: 520px;
	}
	.header-content {
		font-size: 16px;
		line-height: 24px;
		&__span {
			font-size: 28px;
    		line-height: 36px;
		}	
	}
	.main-inner {
		bottom: 0;
		&__h1 {
			font-size: 23px;
			line-height: 31px;
		}
	}
	.main-spruces_left, .main-spruces_right {
		top: 110px;
	}
	.subtitle {
		font-size: 22px;
		line-height: 30px;
		&::before {
			width: 80px;
    		height: 3px;
		}
	}
	.main-text {
		&__paragraph {
			font-size: 16px;
			line-height: 24px;
		}
	}
	.accent {
		font-size: 16px;
		line-height: 24px;
	}
	.video {
		&__link {
			&::before {
				width: 60px;
				height: 60px;
			}
			&::after {
				width: 100px;
				height: 100px;
			}
		}
	}
	.video-gallery__img {
		width: 354px;
	}
	.contacts__list {
		padding-right: 20px;
	}
	.form {
		padding: 30px 40px;
		&__paragraph {
			font-size: 16px;
			line-height: 26px;
		}
	}
	.contacts__title {
		font-size: 28px;
		line-height: 36px;
	}
}
@media screen and (max-width: 767px){
	.container{
		max-width: 600px;
	}
	.header-content {
		&__img {
			display: none;
		}
		&__text_right {
			text-align: left;
		}
	}
	.main {
		padding-bottom: 80px;
	}
	.main-section {
		margin-top: 100px;
	}
	.social {
		margin-bottom: 43px;
		&__icon {
			width: 38px;
			height: 38px;
		}
	}
	.main-inner {
		&__title {
			padding: 16px 65px;
		}
		&__h1 {
			font-size: 18px;
    		line-height: 26px;
		}
		&__btn {
			width: 196px;
			height: 38px;
			font-size: 16px;
		}
	}
	.main-spruces_left, .main-spruces_right {
		width: 200px;
	}
	.host-events {
		&__img {
			width: 170px;
			height: 170px;
		}
		&__name {
			font-size: 16px;
		}
	}
	.video-gallery__link {
		display: block;
		margin: 0 auto 0 auto;
		&:last-child {
			margin-top: 20px;
		}
	}
	.contacts {
		&__title {
			font-size: 20px;
			line-height: 28px;
		}
		&__list {
			flex-direction: column;
			align-items: center;
		}
		&__link {
			margin-left: -50px;
		}
		&__line {
			display: none;
		}
	}
}
// Стили для страницы "Политика конфиденциальности"
.main__title {
	font-size: 26px;
	line-height: 34px;
	margin: 55px 0 0;
}
.politika-kd__paragraph {
	font-size: 16px;
	line-height: 24px;
}
.main-text__paragraph, .main__list {
	font-size: 16px;
	line-height: 24px;
}

@media screen and (max-width: 599px){
	.container{
		max-width: 480px;
	}
	.header__logo {
		margin: 72px auto 40px auto;
	}
	.header-content {
		flex-direction: column;
		justify-content: center;
		&__text {
			text-align: center;
		}
		&__img {
			flex-grow: 0;
		}
		&__text_right {
			margin-top: 24px;
			text-align: center;
		}
	}
	.main-inner {
		position: static;
		&__title {
			padding: 16px 20px;
		}
	}
	.main-spruces_left, .main-spruces_right {
		top: 215px;
	}
	.subtitle {
		display: block;
		font-size: 20px;
		line-height: 26px;
		text-align: center;
		&::before {
			width: 0;
			height: 0;
			margin: 0;
		}
	}
	.host-events {
		flex-direction: column;
		&__wrapper:not(:first-child) {
			margin-top: 32px;
		}
		&__img {
			width: 140px;
			height: 140px;
			margin: 0 auto;
		}
	}
	.video__link {
		&::before {
			width: 40px;
			height: 40px;
		}
		&::after {
			width: 70px;
			height: 70px;
		}
	}
	.video-gallery__link {
		&::before {
			width: 35px;
			height: 35px;
		}
		&::after {
			width: 60px;
			height: 60px;
		}
	}
	.form {
		padding: 30px 20px;
		&__group {
			margin-top: 24px;
		}
	}
	.footer-inner {
		flex-direction: column;
		.social {
			margin: 20px 0;
		}
	}
}
// Стили для страницы "Политика конфиденциальности"
ol, ul {
	padding-left: 20px;
}
.main__link {
	font-size: 16px;
}
@media screen and (max-width: 479px){
	.container{
		max-width: 100%;
	}
	.main-section {
		margin-top: 80px;
	}
	.header-content {
		&__span {
			font-size: 24px;
			line-height: 28px;
		}
	}
	.host-events__name {
		margin-top: 15px;
	}
}
// Стили для страницы "Политика конфиденциальности"
.main__title {
	font-size: 20px;
}
@media screen and (max-width: 379px){
	.gallery {
		flex-direction: column;
	}
	.fancybox__img {
		width: 60%;
	}
	.form {
		&__label {
			font-size: 16px;
		}
		&__input {
			height: 35px;
		}
	}
	&__paragraph {
		font-size: 14px;
		line-height: 18px;
	}
	.contacts__list {
		font-size: 16px;
		padding-right: 0;
		margin-top: 10px;
	}
}